import MinRequest from './MinRequest'
import $global from './../config/global'
import $upload from './../tools/upload'

const minRequest = new MinRequest()
// 请求拦截器
minRequest.interceptors.request((request) => {
    return request
})
// 响应拦截器
minRequest.interceptors.response((response) => {
    return response.data
})
// 设置默认配置:小程序端需要设置基础路径
const baseURL = ""
console.log(baseURL)
minRequest.setConfig((config) => {
    config.baseURL = baseURL
    return config
})
export default {
    //get请求
    doGet(url, data) {
        return minRequest.get(url, data)
    },
    //post请求
    doPost(url, data) {
        return minRequest.post(url, data)
    },
    //获取菜单数据
    getMenuUserTree(data) {
      return minRequest.post('/api/sysMenu/getMenuUserTree', data)
    },
    getUserRoutes(data) {
      return minRequest.post('/api/sysMenu/getUserRoutes', data)
    },
    getLoginOut(data) {
      return minRequest.post('/api/login/loginOut', data)
    },
    //获取用户角色
    getRoleList(data) {
      return minRequest.post('/api/sysRole/getList', data)
    },
    //获取通知信息
    getNoticeList(data) {
      return minRequest.get('/api/notice/getPage', data)
    },
    getNotice(data) {
      return minRequest.post('/api/notice/get', data)
    },
    //获取配置信息
    getConfig(data) {
        return minRequest.post('/api/config/getConfig', data)
    },
    //获取数据字典
    getDictList(data) {
        return minRequest.post('/api/sysDict/getEhCacheList', data)
    },
    //获取token和sessionId
    getAppletToken(data) {
        return minRequest.get('/api/author/getToken', data)
    },
    //更新用户基础数据
    refreshUserInfo(data) {
        return minRequest.post('/api/applet/author/refreshUserInfo', data)
    },
    //获取用户基础信息和注册信息
    getLoginInfo(data) {
        return minRequest.post('/api/login/getLoginInfo', data)
    },
    //获取文件历史
    getFileHistory(data) {
        return minRequest.get('/api/sysAttach/getFileHistory?refId='+data.refId)
    },
    //删除文件
    deleteFile(data) {
        return minRequest.get('/api/sysAttach/deleteFile?id='+data.id)
    },
    //文件上传
    uploadFiles(uploadType, attachType, file) {
        return $upload.uploadFile("/api/wx/sysAttach/uploadFile/" + uploadType + "/" + attachType, file)
    },
    //清理文件缓存
    clearFileCache() {
        return minRequest.get("/api/wx/sysAttach/clearFileCache")
    },
}



